.header {
  color: $reverse-color;
  padding-top: 48px;
  background-color: $color;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 230px;

  @media screen and (min-width: $tablet-size) {
    padding-top: 40px;
  }

  @media screen and (min-width: $desktop-size) {
    padding-top: 46px;
  }
}

.home-header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('../images/home-header_mobile.jpg');

  @media screen and (min-width: $tablet-size) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/home-header_tablet.jpg');
  }

  @media screen and (min-width: $desktop-size) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/home-header_desktop.jpg');
  }
}

.library-header {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
    url('../images/watched-header_mobile.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;

  @media screen and (min-width: $tablet-size) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/watched-header_tablet.jpg');
  }

  @media screen and (min-width: $desktop-size) {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url('../images/watched-header_desktop.jpg');
  }
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.main-nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 38px;
  align-items: baseline;
}

.nav-button-list {
  display: flex;
  list-style: none;
}

.logo {
  text-decoration: none;
  color: inherit;
}

.logo-ico {
  margin-right: 10px;
}

.logo-text {
  display: none;
  @media screen and (min-width: $tablet-size) {
    display: inline;
    font-size: 30px;
    line-height: 1.167;
    font-weight: 500;
  }
}

.nav-button-list__item {
  padding: 0;
  margin-left: 39px;
}

.nav__btn {
  padding: 0;
  border: none;
  font-weight: 500;
  cursor: pointer;
  background-color: transparent;
  color: inherit;
  text-transform: uppercase;
  line-height: 1.167;
}

.nav__btn:hover,
.nav__btn:focus,
.nav__btn.active {
  border-bottom: 3px solid $accent-color;
}

.search-form {
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.search-box {
  width: 280px;
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5px solid $reverse-color;
  padding-bottom: 4px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 22px;

  @media screen and (min-width: $tablet-size) {
    width: 336px;
  }
}

.search-field {
  border: none;
  background: transparent;
  padding: 0;
  font-size: 16px;
  line-height: 1.143;
  outline: none;
  color: $reverse-color;
  font-weight: 500;
  font-style: italic;
  width: 260px;

  @media screen and (min-width: $tablet-size) {
    width: 316px;
  }
}

.search-field::placeholder {
  color: $reverse-color;
}

.search-btn {
  padding: 0;
  border: none;
  cursor: pointer;
  width: 12px;
  height: 12px;
  background-color: transparent;
  background-image: url('../images/search-ico.svg');
  background-size: contain;
}

.warning-message {
  text-align: left;
  font-size: 16px;
  line-height: 1.14;
  color: $error-text-color;
  @media screen and (min-width: $tablet-size) {
    text-align: center;
  }
}

.button-box {
  display: flex;
  justify-content: center;
}

.btn {
  cursor: pointer;
  padding: 14px 38px 12px;
  text-transform: uppercase;
  color: $reverse-color;
  border-radius: 5px;
  border: 1px solid $reverse-color;
  background-color: transparent;
  height: 44px;
  min-width: 130px;
  @media screen and (min-width: $tablet-size) {
    min-width: 136px;
  }
}

.btn--first {
  margin-right: 20px;
  @media screen and (min-width: $tablet-size) {
    margin-right: 15px;
  }
}

.btn:hover,
.btn:focus,
.btn-current {
  border-color: $bg-accent-color;
  background-color: $bg-accent-color;
  box-shadow: $btn-box-shadow;
}
